.list-card-view {
  width: 270px;
  height: 38px;
  position: fixed;
  bottom: 32px;
  border-radius: 44px;
  background-color: rgba(11, 87, 200, 0.05);
  display: flex;
  justify-content: space-between;
  left: 50%;
  transform: translate(-50%, -50%);
}

.list-card-view-text {
  padding: 4px 4px;
  color: white;
}

.list-card-view-text-selected {
  padding: 4px 4px;
  color: white;
  font-weight: bold;
}

.list-card-view-selected {
  width: 123px;
  height: 30px;
  border-radius: 30px;
  background-color: rgba(19, 19, 19, 0.68);
  padding: 4px;
  display: flex;
  justify-content: center;
  cursor: pointer;
}

.list-card-view-unselected {
  width: 123px;
  height: 30px;
  padding: 4px;
  display: flex;
  justify-content: center;
  cursor: pointer;
}

.card-holder {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.music-info-detail {
  background: rgba(0, 0, 0, 0.59);
  box-shadow: inset 0 0px 4px rgba(0, 0, 0, 0.5);
  -webkit-backdrop-filter: blur(18px);
          backdrop-filter: blur(18px);
  position: absolute;
  height: 244px;
  width: 34px;
  left: 0%;
  top: 0%;
  border-radius: 6px 0px 0px 6px;
  display: inline-block;
}

.play-button-icon {
  background: rgba(32, 32, 32, 0.55);
  border: solid 1px linear-gradient(#d9d9d9, #838383);
  box-shadow: inset 0 4px 6px rgba(0, 0, 0, 0.5), 0 6px 13px rgba(0, 0, 0, 0.5);
  height: 90px;
  width: 90px;
  border-radius: 50%;
  position: absolute;
  margin: auto;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  -webkit-backdrop-filter: blur(11px);
          backdrop-filter: blur(11px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

