.profile-container {
  margin: 0 auto;
  padding: 0px 0 100px 0;
  overflow-y: hidden;
  overflow-x: hidden;
}

.profile-detail-container {
  display: flex;
  flex-direction: column;
  align-items: center; 
  padding: 83px 12px 0 12px;
}

.profile-button-option-holder {
  height: 36px;
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.5), 
              inset 0 -1px 2px rgba(0, 0, 0, 0.5), 
              inset 0 -3px 0px rgba(37, 37, 37, 0.5), 
              inset 0 2px 4px rgba(0, 0, 0, 0.5), 
              0 3px 2px rgba(0, 0, 0, 0.13); 
  border-radius: 22px;
  background-image: linear-gradient(180deg, #191919 0%, #121212 100%);
  display: flex;
  justify-content: space-evenly;
  margin: 0 12px;
}

.profile-button-option {
  color: white;
  font-family: Quicksand;
  font-size: 16px;
  font-weight: bold;
  padding-top: 8px;
  padding-left: 14px;
  padding-right: 14px;
  cursor: pointer;
}

.profile-button-option-selected {
  font-family: Quicksand;
  font-size: 16px;
  font-weight: bold;
  padding-top: 8px;
  padding-left: 14px;
  padding-right: 14px;
  cursor: pointer;
  background: linear-gradient(45deg, #239BAE, #D76DB2);
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
  -webkit-text-fill-color: transparent;
}

.profile-bio-description {
  color: white;
  font-family: Quicksand;
  font-size: 16px;
  padding-bottom: 16px;
  padding-left: 3px;
  padding-right: 3px;
  text-align: justify;
  max-width: 600px;
}

@media(min-width: 601px) {
  .profile-bio-description {
    max-width: 572px;
  }
}

.profile-handle-title {
  color: white;
  font-family: Quicksand;
  font-size: 16px;
  font-weight: 400;
  padding-bottom: 16px;
}

.profile-large-title {
  color: #ffffff;
  font-family: Quicksand;
  font-size: 25px;
  font-weight: 700;
  padding-top: 12px;
  padding-bottom: 8px;
}

.profile-medium-title {
  color: white;
  font-family: Quicksand;
  font-size: 16px;
  font-weight: 600;
  padding-top: 2px;
  letter-spacing: 0.4px;
}

